import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import FlightsData from "../FlightsData/FlightsData";
import SearchForm from "../Search/SearchForm";
import PriceFilter from "../Search/PriceFilter";
import "./Flights.scss";

import { Link } from "react-router-dom";

import {
  retriveAllFlightsInfo,
  retrieveFlightsByOrgDestDep,
  retrieveFlightsByOrgDestDepPrice,
  retrieveFlightsByOrgDestDepRetPrice,
  retrieveFlightsByOrgDestDepRet,
  retrieveFlightsByTicketPrice,
} from "../../actions/flight-operations";

const Flights = () => {
  const [origin, setOrigin] = useState(""),
    [destination, setDestination] = useState(""),
    [departureDate, setDepartureDate] = useState(""),
    [returnDate, setReturnDate] = useState(""),
    [passengers, setPassengers] = useState(0),
    [priceRange, setPriceRange] = useState(""),
    flightOperations = useSelector((state) => state.flightOperations),
    [isSearchValid, setIsSearchValid] = useState(false),
    [oneWayTab, setOneWayTab] = useState(true),
    [returnTab, setReturnTab] = useState(false),
    [priceRangeValue, setPriceRangeValue] = useState(10000),
    dispatch = useDispatch();

  useEffect(() => {
    dispatch(retriveAllFlightsInfo());
  }, []);

  const onChangeOrigin = (event) => {
    setOrigin(event.target.value);
  };
  const onChangeDestination = (event) => {
    setDestination(event.target.value);
  };
  const onChangeDepartureDate = (event) => {
    setDepartureDate(event.target.value);
  };
  const onChangeReturnDate = (event) => {
    setReturnDate(event.target.value);
  };
  const onChangepassengersCount = (event) => {
    setPassengers(event.target.value);
  };
  const onChangePriceRange = (event) => {
    setPriceRange(event.target.value);
    setPriceRangeValue(event.target.value);
    if (
      origin === "" &&
      destination === "" &&
      departureDate === "" &&
      returnDate === "" &&
      priceRange !== ""
    ) {
      dispatch(retrieveFlightsByTicketPrice(priceRange));
    }
  };
  const searchFlights = () => {
    if (
      origin !== "" &&
      destination !== "" &&
      departureDate !== "" &&
      priceRange === "" &&
      returnDate === ""
    ) {
      setIsSearchValid(true);
      dispatch(retrieveFlightsByOrgDestDep(origin, destination, departureDate));
    } else if (
      origin !== "" &&
      destination !== "" &&
      departureDate !== "" &&
      priceRange !== "" &&
      returnDate === ""
    ) {
      setIsSearchValid(true);
      dispatch(
        retrieveFlightsByOrgDestDepPrice(
          origin,
          destination,
          departureDate,
          priceRange
        )
      );
    } else if (
      origin !== "" &&
      destination !== "" &&
      departureDate !== "" &&
      priceRange !== "" &&
      returnDate !== ""
    ) {
      setIsSearchValid(true);
      dispatch(
        retrieveFlightsByOrgDestDepRetPrice(
          origin,
          destination,
          departureDate,
          returnDate,
          priceRange
        )
      );
    } else if (
      origin !== "" &&
      destination !== "" &&
      departureDate !== "" &&
      returnDate !== "" &&
      priceRange === ""
    ) {
      setIsSearchValid(true);
      dispatch(
        retrieveFlightsByOrgDestDepRet(
          origin,
          destination,
          departureDate,
          returnDate
        )
      );
    } else if (origin === "" || destination === "" || departureDate === "") {
      setIsSearchValid(false);
    }
  };

  const oneWayTabClickHandler = (event) => {
    if (!oneWayTab) {
      setReturnTab(false);
      setOneWayTab(true);
      setReturnDate("");
    } else {
      setOneWayTab(false);
    }
  };

  const returnTabClickHandler = (event) => {
    if (!returnTab) {
      setOneWayTab(false);
      setReturnTab(true);
    } else {
      setReturnTab(false);
    }
  };

  const resetForm = () => {
    setOrigin("");
    setDestination("");
    setDepartureDate("");
    setReturnDate("");
    setPassengers(0);
    setPriceRange("");
  };

  return (
    <div className="row flights-row">
      <div className="col-5">
        <h3 className="filter-title">Filters</h3>
        <ul className="journey-type">
          <li>
            <a
              href="#"
              className={`journey ${oneWayTab && "active"}`}
              onClick={oneWayTabClickHandler}
            >
              One Way
            </a>
          </li>
          <li>
            <a
              href="#"
              className={`journey ${returnTab && "active"}`}
              onClick={returnTabClickHandler}
            >
              Return
            </a>
          </li>
        </ul>
        <form className="search-form" noValidate="">
          <div className="form-group">
            <input
              id="origin"
              name="origin"
              label="Origin"
              type="text"
              className="form-control"
              value={origin}
              onChange={onChangeOrigin}
              placeholder="Enter Origin City"
            />
          </div>
          <div className="form-group">
            <input
              id="destination"
              name="destination"
              label="Destination"
              type="text"
              className="form-control"
              value={destination}
              onChange={onChangeDestination}
              placeholder="Enter Destination City"
            />
          </div>
          <div className="form-group">
            <input
              id="departureDate"
              name="departureDate"
              label="Departure Date"
              type="date"
              className="form-control"
              value={departureDate}
              onChange={onChangeDepartureDate}
              placeholder="Enter Departure Date"
            />
          </div>
          <div className="form-group">
            {returnTab && (
              <input
                id="returnDate"
                name="returnDate"
                label="Return Date"
                type="date"
                className="form-control"
                value={returnDate}
                onChange={onChangeReturnDate}
                placeholder="Enter Return Date"
              />
            )}
          </div>
          <div className="form-group">
            <input
              id="passengers"
              name="passengers"
              label="Passengers"
              type="number"
              className="form-control"
              value={passengers}
              onChange={onChangepassengersCount}
              placeholder="Passengers"
            />
          </div>
          <div className="form-group button-wrapper">
            <button
              type="button"
              name="search-button"
              className="btn search"
              onClick={searchFlights}
            >
              Search
            </button>
            <a href="#" className="reset-search" onClick={resetForm}>
              Reset all
            </a>
          </div>
        </form>
        <h4 className="refine-search">Refine flight search</h4>
        <input
          type="range"
          min="0"
          max="10000"
          value={priceRange}
          onChange={onChangePriceRange}
          className="slider"
          id="myRange"
        />
        <div className="row price-range-indicator">
          <span className="lower-range">0</span>
          <span className="higher-range">{priceRangeValue}</span>
        </div>
      </div>
      <div className="col-7">
        {isSearchValid && (
          <div className="searched-for">
            {origin && destination && !returnTab && (
              <h1>
                {origin} &gt; {destination}
              </h1>
            )}
            {origin && destination && returnTab && (
              <h1>
                {origin} &gt; {destination} &gt; {origin}
              </h1>
            )}
            {departureDate && <p>Depart: {departureDate}</p>}
            {returnDate && <p>Arrive: {returnDate}</p>}
          </div>
        )}

        {flightOperations &&
          flightOperations.map((flightOperation, index) => (
            <FlightsData
              key={flightOperation["id"]}
              id={flightOperation["id"]}
              price={flightOperation["ticket_price"]}
              flightNumber={flightOperation["flight_number"]}
              flightName={flightOperation["flight_name"]}
              flightOrigin={flightOperation["flight_origin"]}
              flightDesitination={flightOperation["flight_destination"]}
              flightOriginCode={flightOperation["flight_origin_code"]}
              flightDesitinationCode={
                flightOperation["flight_destination_code"]
              }
              departureDate={flightOperation["departure_date"]}
              originDepartureTime={flightOperation["origin_departure_time"]}
              originArrivalTime={flightOperation["origin_arrival_time"]}
              returnDepartureTime={flightOperation["return_departure_time"]}
              returnArrivalTime={flightOperation["return_arrival_time"]}
              isReturnDate={returnDate}
            />
          ))}
      </div>
    </div>
  );
};

export default Flights;
