import {
  RETRIEVE_ALL_FLIGHTS_INFO,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_DATE,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_RETURN_DATE,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_DATE_TICKET_PRICE,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_RETURN_DATE_TICKET_PRICE,
  RETRIEVE_ALL_FLIGHTS_BY_TICKET_PRICE,
} from "./types";

import FlightDataService from "../services/flight-data-service";

export const retriveAllFlightsInfo = () => async (dispatch) => {
  try {
    const response = await FlightDataService.findAllFlightInfos();
    dispatch({
      type: RETRIEVE_ALL_FLIGHTS_INFO,
      payload: response.data,
    });
  } catch (err) {
    console.log(err);
  }
};

export const retrieveFlightsByOrgDestDep =
  (origin, destination, departureDate) => async (dispatch) => {
    try {
      const response = await FlightDataService.findFlightsByOrgDestDep(
        origin,
        destination,
        departureDate
      );
      dispatch({
        type: RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_DATE,
        payload: response.data,
      });
    } catch (err) {
      console.log(err);
    }
  };

