import http from "../http-common";

class FlightDataService {
  findAllFlightInfos() {
    return http.get("/flight_info/");
  }

  findFlightsByOrgDestDep(origin, destination, departureDate) {
    return http.get(
      `/flight_info?origin=${origin}&destination=${destination}&departure_date=${departureDate}`
    );
  }

  findFlightsByOrgDestDepPrice(
    origin,
    destination,
    departureDate,
    ticketPrice
  ) {
    return http.get(
      `/flight_info?origin=${origin}&destination=${destination}&departure_date=${departureDate}&ticket_price=${ticketPrice}`
    );
  }

  findFlightsByOrgDestDepRet(origin, destination, departureDate, returnDate) {
    return http.get(
      `/flight_info?origin=${origin}&destination=${destination}&departure_date=${departureDate}&return_date=${returnDate}`
    );
  }

  findFlightsByOrgDestDepRetPrice(
    origin,
    destination,
    departureDate,
    returnDate,
    ticketPrice
  ) {
    return http.get(
      `/flight_info?origin=${origin}&destination=${destination}&departure_date=${departureDate}&return_date=${returnDate}&ticket_price=${ticketPrice}`
    );
  }

  findFlightsByTicketPrice(ticketPrice) {
    return http.get(`/flight_info?ticket_price=${ticketPrice}`);
  }
}

export default new FlightDataService();
