import {
  RETRIEVE_ALL_FLIGHTS_INFO,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_DATE,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_RETURN_DATE,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_DATE_TICKET_PRICE,
  RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_RETURN_DATE_TICKET_PRICE,
  RETRIEVE_ALL_FLIGHTS_BY_TICKET_PRICE,
} from "../actions/types";

const initialState = [];

function FlightOperationReducer(flightOperations = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case RETRIEVE_ALL_FLIGHTS_INFO:
      return payload;

    case RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_DATE:
      return payload;

    case RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_RETURN_DATE:
      return payload;

    case RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_DATE_TICKET_PRICE:
      return payload;

    case RETRIVE_FLIGHTS_BY_ORIGIN_DESTINATION_DEPARTURE_RETURN_DATE_TICKET_PRICE:
      return payload;

    case RETRIEVE_ALL_FLIGHTS_BY_TICKET_PRICE:
      return payload;

    default:
      return flightOperations;
  }
}

export default FlightOperationReducer;
