import React from "react";
import "./FlightsData.scss";

const FlightsData = (props) => {
  return (
    <div className="flight-data" key={props.id}>
      <div className="flight-info">
        <div className="price">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 32 32"
            width="20px"
            height="20px"
            fill="#333333"
          >
            <path d="M21.482 7.945h3.536c.982 0 1.786.818 1.786 1.818s-.804 1.818-1.786 1.818h-3.536a9.429 9.429 0 01-2.625 5.109 9.509 9.509 0 01-6.75 2.891h-.679l9.661 9.255c0 .018.018.018.036.036.679.673.696 1.782.036 2.473a1.742 1.742 0 01-2.518.091L5.714 19a1.78 1.78 0 01-.554-1.364c.036-.964.839-1.727 1.786-1.691h5.179a5.902 5.902 0 004.214-1.836 6.327 6.327 0 001.482-2.527H6.946c-.982 0-1.786-.818-1.786-1.818s.804-1.818 1.786-1.818h10.875C17 5.455 14.714 3.782 12.125 3.764H6.946c-.982 0-1.786-.818-1.786-1.818S5.964.128 6.946.128h18.071c.982 0 1.786.818 1.786 1.818s-.804 1.818-1.804 1.818h-5.464a8.504 8.504 0 011.946 4.182z"></path>
          </svg>
          {props.price} -{" "}
          <span className="flight-name">{props.flightName}</span>
        </div>
        <div className="row flight-timings">
          <div className="col-6">
            <h3>{props.flightNumber}</h3>
            <div className="location">
              <span>{props.flightOriginCode}</span> <span>&gt;</span>
              <span> {props.flightDesitinationCode}</span>
            </div>
            <h4 className="depart-time">Depart: {props.originDepartureTime}</h4>
            <h4 className="arrival-time">Arrive: {props.originArrivalTime}</h4>
          </div>
          {props.isReturnDate && (
            <div className="col-6">
              <h3>{props.flightNumber}</h3>
              <div className="location">
                <span>{props.flightDesitinationCode}</span> <span>&gt;</span>
                <span>{props.flightOriginCode}</span>
              </div>
              <h4 className="depart-time">
                Depart: {props.returnDepartureTime}
              </h4>
              <h4 className="arrival-time">
                Arrive: {props.returnArrivalTime}
              </h4>
            </div>
          )}
        </div>
        <div className="btn-wrapper">
          <button type="button" className="btn" key={props.id}>
            Book Flight
          </button>
        </div>
      </div>
    </div>
  );
};

export default FlightsData;
